import React, { useState } from 'react'
import AdminServices from 'services/api/AdminServices'
import MainHeader from "views/mobileView/components/molecules/Headers/MainHeader";
import cssVar from 'views/mobileView/styles';
import { toast } from 'react-toastify';
import db from 'libs/db';
import CustomAccordion from 'views/mobileView/components/atoms/Accordion';
import { Chip } from '@mui/material';
import { CurrencyFormat } from 'views/mobileView/components/atoms/CurrencyFormat';
import addToRecentlyViewed from 'views/mobileView/components/atoms/addToRecentlyViewed';
import DisplayRecentlyViewed from 'views/mobileView/components/atoms/DisplayRecentlyViewed';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import AMCIcon from 'views/mobileView/components/atoms/AMCIcon';
import PauseMFModal from 'views/mobileView/components/molecules/Cards/PausedMFModal';
import PerformanceChart from 'views/mobileView/components/molecules/Cards/PerformanceChart';

function MutualFundDetail(props) {
    const FUNDCODE = props.match.params.FundId;

    const [fund, setFund] = useState({});
    const [peerReturns, setPeerReturns] = useState({})
    const [rollingReturns, setRollingReturns] = useState({})
    const [topHoldings, setTopHoldings] = useState([])
    const [sectorHoldings, setSectorHoldings] = useState([])
    const [isWatchlisted, setIsWatchlisted] = useState(false);
    const [user, setUser] = useState({})
    const [fundNotFound,setFundNotFound] = useState(false)
    const [loading, setLoading] = useState(true)
    const [showPausedMfModal, setShowPausedMFModal] = React.useState(false)

    const defaultChartOptions = {
        title: {
            text: null
        },
        yAxis: {
            title: {
                text: ''
            },
            endOnTick: true,
            startOnTick: true,
            labels: {
                align: 'left'
            }
        },

        xAxis: {
            type: 'datetime',
            dateTimeLabelFormats: {
                day: '%d %b %Y'    //ex- 01 Jan 2016
            },
            labels: {
                format: "{value:%Y}",
                style: {
                    color: "#ababab",
                    fontSize: "12px"
                }
            },
            crosshair: {
                width: 1,
                color: 'grey'
            },
            lineColor: "#e5e5e5",
            tickLength: 0,
            startOnTick: true,
            // endOnTick: false,
        },

        legend: {
            enabled: false
        },

        plotOptions: {
            series: {
                label: {
                    connectorAllowed: false
                },
                turboThreshold: 51000,//larger threshold or set to 0 to disable
                marker: {
                    enabled: false
                }
            }
        },
        tooltip: {
            shared: true
        },
        credits: {
            enabled: false
        },
        responsive: {
            rules: [{
                condition: {
                    maxWidth: 400
                },
            }]
        }
    }
    React.useEffect(() => {
        loadData();
    }, [FUNDCODE])

    const loadData = async () => {
        try {
            db.get(db.model.user).then(async (p) => {
                setUser(p.userData)
                await getWatchlist(p.userData.id)
                await getMutualFundById().then((fund) => {
                    if(Object.keys(fund).length){
                        addToRecentlyViewed({
                            type: 2,
                            ID: FUNDCODE,
                            Name: fund.SchemeName,
                            XIRR: fund.FiveYrReturn,
                            Category: fund.SubCategoryName,
                            AMCName: fund.AMCName,
                            MinInv: fund.MinSIPInvestAmount
                        })
                        getPeerReturnsByFundCode();
                        // getRollingReturnsByFundCode();
                        getTopHoldings();
                        getSectorHoldings();
                    } else {
                        setFundNotFound(true)
                    }
                })
                setLoading(false)
            });
        } catch (error) {
            console.log(error)
        }
    }

    const getMutualFundById = async () => {
        try {
            let res = await AdminServices.getFundByFundCode(FUNDCODE).then(res => res)
            if (res.type) {
                console.log("Get MF by id API", res.data)
                setFund(res.data)
                return res.data
            }
            else {
                setFund({})
                toast.error("Could not find fund")
                return {}
            }
        } catch (error) {
            toast.error(error)
        }
    }

    const getPeerReturnsByFundCode = async () => {
        try {
            let res = await AdminServices.getPeerReturnsByFundCode(FUNDCODE).then(res => res)
            if (res.type) {
                console.log("Get Peer Returns by id API", res.data)
                setPeerReturns(res.data)
            }
            else {
                setPeerReturns({})
            }
        } catch (error) {
            toast.error(error)
        }
    }
    const getRollingReturnsByFundCode = async () => {
        try {
            let res = await AdminServices.getRollingReturnsByFundCode(FUNDCODE).then(res => res)
            if (res.type) {
                console.log("Get Rolling Returns by id API", res.data)
                setRollingReturns(res.data)
            }
            else {
                setRollingReturns({})
            }
        } catch (error) {
            toast.error(error)
        }
    }

    const getTopHoldings = async () => {
        try {
            let res = await AdminServices.getTopHoldings(FUNDCODE).then(res => res)
            if (res.type) {
                console.log("Get getTopHoldings by id API", res.data)
                setTopHoldings(res.data)
            }
            else {
                setTopHoldings({})
            }
        } catch (error) {
            toast.error(error)
        }
    }

    const getSectorHoldings = async () => {
        try {
            let res = await AdminServices.getSectorHoldings(FUNDCODE).then(res => res)
            if (res.type) {
                console.log("Get getSectorHoldings by id API", res.data)
                setSectorHoldings(res.data)
            }
            else {
                setSectorHoldings({})
            }
        } catch (error) {
            toast.error(error)
        }
    }
    
    const getWatchlist = async (id) => {
        try {
            db.get(db.model.watchlist).then(async (p) => {
                if (p === null) {
                    let res = await AdminServices.getWatchlist(id).then(res => res);
                    if (res.type === true) {
                        await db.set(db.model.watchlist, JSON.stringify(res.data))
                        setIsWatchlisted(res.data.some(f => f.FundType === 2 && f.FundCode === FUNDCODE))
                    }
                } else {
                    setIsWatchlisted(p.some(f => f.FundType === 2 && f.FundCode === FUNDCODE))
                }
            });
        } catch (error) {
            toast.error(error);
        }
    }

    const updateWatchlist = async () => {
        const data = {
            "clientId": user.id,
            "fundType": 2,
            "fundCode": FUNDCODE,
            "isActive": isWatchlisted ? 0 : 1
        }
        try {
            let res = await AdminServices.updateWatchlist(data).then(res => res)
            if (res.type) {
                setIsWatchlisted(!isWatchlisted)
            }
        } catch (error) {
            console.log(error)
        }

    }

    const Summary = () => {
        return (
            <div style={{ background: cssVar.getPropertyValue('--darkGreen'), padding: "4%", display: "flex", gap: "4%" }}>
                <AMCIcon amcName={fund.AMCName} height="42px" width="42px" />
                <div style={{ flex: 1 }}>
                    <div className='header1dark' style={{ color: cssVar.getPropertyValue('--white'), minHeight: "44px" }}>{fund.SchemeName}</div>
                    <div style={{ display: "flex", marginTop: "4%" }}>
                        <div style={{ flex: 1 }}>
                            <div className="bodyTextLight" style={{ color: cssVar.getPropertyValue('--subtle') }}>5Y CAGR</div>
                            <div className="header1dark" style={{ color: cssVar.getPropertyValue("--primaryGreen") }}>{parseFloat(fund.FiveYrReturn || 0).toFixed(2)} %</div>
                        </div>
                        <div style={{ flex: 1 }}>
                            <div className="bodyTextLight" style={{ color: cssVar.getPropertyValue('--subtle') }}>Category</div>
                            <div className='header1dark' style={{ color: cssVar.getPropertyValue('--white') }}>{fund.SubCategoryName}</div>
                        </div>
                    </div>
                    <div style={{ display: "flex", marginTop: "4%" }}>
                        <div style={{ flex: 1 }}>
                            <div className="bodyTextLight" style={{ color: cssVar.getPropertyValue('--subtle') }}>NAV</div>
                            <div className='header1dark' style={{ color: cssVar.getPropertyValue('--white') }}>{CurrencyFormat(fund.NAV, 'long', 2)}</div>
                        </div>
                        <div style={{ flex: 1 }}>
                            <div className="bodyTextLight" style={{ color: cssVar.getPropertyValue('--subtle') }}>Min investment</div>
                            <div className='header1dark' style={{ color: cssVar.getPropertyValue('--white') }}>{CurrencyFormat(fund.MinSIPInvestAmount, 'long', 2)}</div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    const CompWithPeers = () => {
        return (
            <>
                {Object.keys(peerReturns).length !== 0 ?
                    <div style={{ textAlign: "right", padding: "4%" }}>
                        <div style={{ display: "flex", paddingBottom: "14px" }}>
                            <div style={{ flex: 2 }}></div>
                            <div className='smallTextLight' style={{ flex: 1 }}>6 M</div>
                            <div className='smallTextLight' style={{ flex: 1 }}>1 Y</div>
                            <div className='smallTextLight' style={{ flex: 1 }}>3 Y</div>
                            <div className='smallTextLight' style={{ flex: 1 }}>5 Y</div>
                        </div>
                        <div style={{ display: "flex", paddingBottom: "14px" }}>
                            <div className='header2dark' style={{ flex: 2, color: cssVar.getPropertyValue('--darkGreen'), textAlign: "left" }}>This fund</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{peerReturns.Fund6month?.toFixed(1)}%</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{peerReturns.Fund1year?.toFixed(1)}%</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{peerReturns.Fund3year?.toFixed(1)}%</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{peerReturns.Fund5year?.toFixed(1)}%</div>
                        </div>
                        <div style={{ display: "flex", paddingBottom: "14px" }}>
                            <div className='header2light' style={{ flex: 2, textAlign: "left" }}>Category</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{peerReturns.Cat6month?.toFixed(1)}%</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{peerReturns.Cat1year?.toFixed(1)}%</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{peerReturns.Cat3year?.toFixed(1)}%</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{peerReturns.Cat5year?.toFixed(1)}%</div>
                        </div>
                        <div style={{ display: "flex", paddingBottom: "14px" }}>
                            <div className='header2dark' style={{ flex: 2, color: cssVar.getPropertyValue('--darkYellow'), textAlign: "left" }}>NIFTY 50</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{peerReturns.Index6month?.toFixed(1)}%</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{peerReturns.Index1year?.toFixed(1)}%</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{peerReturns.Index3year?.toFixed(1)}%</div>
                            <div className='bodyTextDark' style={{ flex: 1 }}>{peerReturns.Index5year?.toFixed(1)}%</div>
                        </div>
                    </div>
                    : 
                    <div className='text-center bodyTextLight my-4'>
                        Data not avaliable
                    </div>
                }
            </>
        )
    }
    const AUM = () => {
        return (
            <>
                <div style={{ display: "flex", padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight' style={{ marginBottom: "1%" }}>AUM</div>
                        <div className='bodyTextDark'>{CurrencyFormat(fund.AUM, 'long', 2)} crore</div>
                    </div>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight' style={{ marginBottom: "1%" }}>Expense ratio</div>
                        <div className='bodyTextDark'>{parseFloat(fund.ExpenseRatio || 0).toFixed(2)} %</div>
                    </div>
                </div>
                <div style={{ display: "flex", padding: "4%" }}>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight' style={{ marginBottom: "1%" }}>Fund manager</div>
                        <div className='bodyTextDark'>
                            {
                                fund?.FundManager?.split(':').map((manager, i) => {
                                    if (i === 0) { return manager }
                                    else { return ', ' + manager }
                                })
                            }
                        </div>
                    </div>
                </div>
            </>
        )
    }

    const RollingReturns = () => {
        const chipset = [
            { key: 1, name: "1 year" },
            { key: 3, name: "3 years" },
            { key: 5, name: "5 years" },
            { key: 10, name: "10 years" },
        ]
        const [duration, setDuration] = React.useState(1);
        const [graphArray, setGraphArray] = React.useState([]);
        const [avgRR, setAvgRR] = React.useState();
        const [sd, setSd] = React.useState();

        React.useEffect(() => {
            switch (duration) {
                case 1: setGraphArray(rollingReturns.OneYrRRData);
                    setAvgRR(rollingReturns.OneYrAvgRR?.toFixed(2));
                    setSd(rollingReturns.OneYrSD?.toFixed(2));
                    return;
                case 3: setGraphArray(rollingReturns.ThreeYrRRData);
                    setAvgRR(rollingReturns.ThreeYrAvgRR?.toFixed(2));
                    setSd(rollingReturns.ThreeYrSD?.toFixed(2));
                    return;
                case 5: setGraphArray(rollingReturns.FiveYrRRData);
                    setAvgRR(rollingReturns.FiveYrAvgRR?.toFixed(2));
                    setSd(rollingReturns.FiveYrSD?.toFixed(2));
                    return;
                case 10: setGraphArray(rollingReturns.TenYrRRData);
                    setAvgRR(rollingReturns.TenYrAvgRR?.toFixed(2));
                    setSd(rollingReturns.TenYrSD?.toFixed(2));
                    return;
                default: setGraphArray([]);
                    setAvgRR(0);
                    setSd(0);
            }
        }, [duration, rollingReturns])

        const options = {
            ...defaultChartOptions,
            series: [
                {
                    name: 'Rolling return',
                    color: cssVar.getPropertyValue('--darkGreen'),
                    data: graphArray?.map(d => [new Date(d["date"]).getTime(), d["rr"]])
                },
                {
                    name: 'Avg rolling return',
                    color: '#F5A447',
                    data: graphArray?.map(d => [new Date(d["date"]).getTime(), parseFloat(avgRR)])
                },
            ]
        }
        return (
            <div style={{ padding: "4%" }}>
                {chipset.map((item) => {
                    return (
                        <Chip
                            className={duration === item.key ? "normalChip selected" : "normalChip"}
                            key={item.key}
                            label={item.name}
                            onClick={() => {
                                setDuration(item.key)
                            }}
                        />
                    )
                })}
                <div className='bodyTextLight' style={{ paddingTop: "4%" }}>
                    Avg rolling return<span style={{ color: cssVar.getPropertyValue('--darkGreen') }}> {avgRR}%</span>
                </div>
                <div className='bodyTextLight' style={{ paddingTop: "2%" }}>
                    Standard deviation<span style={{ color: "#F5A447" }}> {sd}%</span>
                </div>
                {/* <div>
                    <HighchartsReact
                        highcharts={Highcharts}
                        options={options}
                    />
                </div> */}
            </div>
        )
    }
    const MinSubscription = () => {
        return (
            <>
                <div style={{ display: "flex", padding: "4%", borderBottom: "1px solid #e5e5e5" }}>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight' style={{ marginBottom: "1%" }}>Minimum SIP</div>
                        <div className='bodyTextDark'>{CurrencyFormat(fund.MinSIPInvestAmount, 'long', 2)}</div>
                    </div>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight' style={{ marginBottom: "1%" }}>Minimum lumpsum</div>
                        <div className='bodyTextDark'>{CurrencyFormat(fund.MinLumpsumInvestAmount, 'long', 2)}</div>
                    </div>
                </div>
                <div style={{ display: "flex", padding: "4%" }}>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight' style={{ marginBottom: "1%" }}>Minimum redemption</div>
                        <div className='bodyTextDark'>{CurrencyFormat(fund.MinRedemptionAmount, 'long', 2)}</div>
                    </div>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight' style={{ marginBottom: "1%" }}>Minimum additional</div>
                        <div className='bodyTextDark'>{CurrencyFormat(fund.MinSubsequentAmount, 'long', 2)}</div>
                    </div>
                </div>
            </>
        )
    }
    const TopHolding = () => {
        const [selection, setSelection] = useState(0)
        return (
            <>
                <div style={{ display: "flex", padding: "3% 4% 0 4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light'), alignItems: "end" }}>
                    <div style={{ flex: 2 }}>
                        <Chip
                            style={{ marginRight: "8%" }}
                            className={selection === 0 ? "specialTab selected" : "specialTab"}
                            label="Companies"
                            onClick={() => {
                                setSelection(0)
                            }}
                        />
                        <Chip
                            className={selection === 1 ? "specialTab selected" : "specialTab"}
                            label="Sectors"
                            onClick={() => {
                                setSelection(1)
                            }}
                        />
                    </div>
                    <div className='header2light' style={{ flex: 1, textAlign: "right",  paddingBottom:'2%' }}>
                        Allocation (%)
                    </div>
                </div>
                {
                    selection === 0 ?
                        <>
                            {
                                topHoldings?.lenght?
                                    topHoldings?.map((c, i) => {
                                        return <div key={i} style={{ display: "flex", padding: "3% 4%", alignItems: "center", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
                                            <div style={{ flex: 2 }}>
                                                <div className='bodyTextDark'>{c.ShortName}</div>
                                            </div>
                                            <div style={{ flex: 1 }}>
                                                <div className='header1faint' style={{ textAlign: 'right' }}>{Number(c.Percentage).toFixed(1)}%</div>
                                            </div>
                                        </div>
                                    })
                                    :
                                    <div className='bodyTextDark' style={{margin:'4%',textAlign:'center'}}>No data Available</div>
                            }
                        </>
                        :
                        <>
                            {
                                sectorHoldings?.length?
                                    sectorHoldings?.map((s, i) => {
                                        return <div key={i} style={{ display: "flex", padding: "3% 4%", alignItems: "center", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
                                            <div style={{ flex: 2 }}>
                                                <div className='bodyTextDark'>{s.Sector}</div>
                                            </div>
                                            <div style={{ flex: 1 }}>
                                                <div className='header1faint' style={{ textAlign: 'right' }}>{Number(s.Percentage).toFixed(1)}%</div>
                                            </div>
                                        </div>
                                    })
                                    :
                                    <div className='bodyTextDark' style={{margin:'4%',textAlign:'center'}}>No data Available</div>
                        }
                        </>
                }
            </>
        )
    }
    const ExitLoad = () => {
        return (
            <>
                <div style={{ display: "flex", padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }}>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight'>Exit load</div>
                        <div className='bodyTextDark'>{fund.ExpenseNote || '-'}</div>
                        {/* <div className='smallTextLight'>for units in excess of 10% of your investment value</div> */}
                    </div>
                </div>
                <div style={{ display: "flex", padding: "4%" }}>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight'>Lock-in</div>
                        <div className='bodyTextDark'>{fund.LockIn || 'None'}</div>
                    </div>
                    <div style={{ flex: 1 }}>
                        <div className='smallTextLight'>Taxation</div>
                        <div className='bodyTextDark'>{fund.TaxTreatment}</div>
                    </div>
                </div>
            </>
        )
    }
    const accordionArray = [
        { key: "1", title: "Comparison with peers", body: CompWithPeers() },
        { key: "2", title: "AUM, fund manager and expense ratio", body: AUM() },
        // { key: "4", title: "Rolling returns and standard deviation", body: RollingReturns() },
        { key: "5", title: "Minimum subscriptions", body: MinSubscription() },
        { key: "6", title: "Top holdings in the fund", body: TopHolding() },
        { key: "7", title: "Exit load, lock-in and taxation", body: ExitLoad() },
    ]
    const Footer = () => {
        return (
            <div className='footer'>
                <button
                    className='solidButton'
                    // onClick={() => props.history.push({
                    //     pathname: '/portfolio/confirm-invest',
                    //     state: {
                    //         fundData: {...fund, MinSIPAmount: fund.MinLumpsumInvestAmount},
                    //         type: 2
                    //     }
                    // })}
                    onClick={()=>setShowPausedMFModal(true) }
                >
                    Invest in this fund
                </button>
            </div>
        )
    }

    return (
        <>
            <div className='mainaContainer'>
                {
                    loading ?
                        <>
                            <MainHeader
                                header="Mutual fund"
                                navigation={props.history}
                            />
                            <ActivityLoaderMintbox />
                        </>
                        :
                        <>
                            <MainHeader
                                header="Direct mutual fund"
                                isWatchlisted={isWatchlisted}
                                onWatchlistIconClick={() => { updateWatchlist() }}
                                right={fundNotFound?'':"bookmark-share"}
                                navigation={props.history}
                            />
                            {
                                fundNotFound?
                                    <div className='bigTextDark text-center mt-5'>Fund data not available</div>
                                    :
                                    <>
                                        <div className="dataContainer px-0">
                                            <Summary />
                                            <PerformanceChart type={2} fundCode={FUNDCODE} />

                                            <CustomAccordion accordionArray={accordionArray} />
                                            <DisplayRecentlyViewed navigation={props.history} header="Recently viewed Mutual Funds" type={2} currentFundId={FUNDCODE} />
                                        </div>
                                        <Footer />
                                    </>
                            }
                            <PauseMFModal navigation={props.history} showModal={showPausedMfModal} onClose={()=>setShowPausedMFModal(false)}/>

                        </>
                }
            </div>
        </>
    )
}

export default MutualFundDetail